import styled from 'styled-components';

const Lena = () => {
    return ( 
        <PaginaLena>
        <CajaLena>
                <h1>Lena en la Red</h1>
                <p>Si tienes un minuto, por favor lee esto:</p>
                <p>Me llamo Jairo, soy desarrollador web y vecino del concejo de Lena.</p>
                <p>Quiero aprovechar mis conocimiento en desarrollo para apoyar a los pequeños negocios locales y darles visibilidad en la web, ya que en estos momentos tan difíciles que vivimos creo que toda ayuda es buena.</p>
                <p>Para mí, es una forma de publicitar y apoyar a los pequeños empresarios del concejo.</p>
                <p>Si tienes un negocio en el concejo de Lena y no tienes pagina Web, no dudes en contactar conmigo y te haré una <span>TOTALMENTE GRATIS.</span></p>
                <p>Si ya dispones de web y quieres que la revise para intentar mejorarla, contactame y haremos una auditoria <span>GRATIS.</span></p>
                <p>Si no tienes un negocio pero conoces a alguien a quien le pueda interesar, no dudes en compartir este mensaje.</p>
            <Monigote src="/desing/lena/seo.png"></Monigote>
            </CajaLena>
            <CajaDetalles>
                <h2>Detalles:</h2>
                <p>-Esta campaña es sin ánimo de lucro, solo es mi pequeño grano de arena para apoyar el comercio local.</p>
                <p>-Me ofrezco a colaborar de manera <span>Totalmente gratuita</span> para desarrollar una página web(valorada en 200€) para cualquiera que la necesite.</p>
                <p>-La web será una página sencilla para hacer publicidad de tu negocio, afición o lo que desees.</p>
                <p>-No constará de Categorías ni pasarelas de pagos.</p>
                <p>-No puedo estimar plazos de entrega ya que dependerá de la carga de trabajo.</p>
                <p>-Si ya dispones de página web haré <span>totalmente GRATIS</span> una auditoría para intentar actualizarla.</p>
                <p>-Si lo que deseas es una web más compleja, con categorías, tienda, etc,... no dudes en comentarmelo y te haré un presupuesto a medida.</p>
            </CajaDetalles>
        
        <CajaLLena>
            <h1>L.lena na Rede</h1>
            <p>Si tienes un minutu, por favor llee esto:</p>
            <p>Llámome Jairo, soi desarrollaor web y vecín del conceyu de L.lena.</p>
            <p>Quiero aprovechar les mios conocencies en desarrollu para sofitar a los pequeños negocios locales y da-yos visibilidá na web, yá que nestos momentos tan difíciles que vivimos creo que toa ayuda ye bona.</p>
            <p>Para mi ye una forma d´espublizar y sofitar a los pequeños empresarios del conceyu.</p>
            <p>Si tienes un negociu nel conceyu de L.lena y nun tienes páxina web, nun duldar en contactar conmigo y te voy faer una <span>TOTALMENTE GRATIS.</span></p>
            <p>Si yá dispones de web y quies que la revise para intentar ameyorala, contactame y vamos faer una auditoria <span>GRATIS.</span></p>
            <p>Si nun tienes un axuxtu pero conoces a daquién pueda interesa-y, nun duldar en compartir esti mensaxe.</p>
        </CajaLLena>  
        <Diseño src="/desing/lena/bro.png"></Diseño>   
        </PaginaLena>
     );
}
const PaginaLena=styled.div`
    width:330px;
    text-align:center;
    font-size:16px;
    margin:auto;
    height:auto;
    display:grid;
    h1, h2{
        color:#CF7500;
    }
    span{
        font-weight:700;
    }
    @media(min-width:768px){
        width:80%;
        grid-template-columns:repeat(2,1fr);
    }
`;
const CajaLena=styled.div`
border-bottom:2px solid #F0A500;
@media(min-width:768px){
        border:2px solid #F0A500;
        border-radius:10px;
        margin:10px 5px 5px;
        padding:5px 55px;
}
`;
const CajaDetalles=styled.div`
@media(min-width:768px){
        border:2px solid #F0A500;
        border-radius:10px;
        margin:10px 5px 5px;
        padding:5px 55px;
}
`;
const CajaLLena=styled.div`
@media(min-width:768px){
        border:2px solid #F0A500;
        border-radius:10px;
        margin:10px 5px 5px;
        padding:5px 55px;
}

`;
const Monigote=styled.img`
    @media(min-width:768px){
        display:none;
}
`;
const Diseño=styled.img`
    @media(min-width:768px){
        margin:60px auto 0px 200px;
        width:400px;
}
`;
export default Lena;